* {
  box-sizing: border-box;
}

body {
  margin: 0;
  border: none;
  outline: none;
  font-family: 'Baloo 2', cursive;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(121, 123, 141);
}

@media (min-width: 768px) {
  body {
    overflow: hidden;
  }
}
